.movie-view-wrapper {
  display: grid;
  
  grid-template-columns: 1fr 1.3fr;
}

.movie-view-details-side {
  height: 1200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  color: white;
  padding-left: 80px;
}

.movie-title {
  font-size: 3em;
  font-weight: 700;
  margin: 20px 0;
}

.movie-genre-runtime {
  margin: 0;
  letter-spacing: 2px;
  font-size: 0.9em;
  color: rgba(255, 255, 255, 0.7);
}

.movie-buttons {
  margin: 55px 0 100px 0;
}

.movie-overview-label {
  letter-spacing: 2px;
}

.movie-overview-text {
  color: rgba(255, 255, 255, 0.5);
  line-height: 25px;
}

.movie-view-img-side {
  height: 1200px;
}

.movie-view-img-side-content-container {
  background: -webkit-gradient(linear,left top, right top,from(var(--mainViewBgColor)), to(rgba(46, 41, 58, 0.7)));
  background: -webkit-linear-gradient(left,var(--mainViewBgColor), rgba(46, 41, 58, 0.7));
  background: -o-linear-gradient(left,var(--mainViewBgColor), rgba(46, 41, 58, 0.7));
  background: linear-gradient(to right,var(--mainViewBgColor), rgba(46, 41, 58, 0.7));
  height: 1200px;
}


