body {
  background: var(--mainViewBgColor);
}

.search-view-wrapper {
  width: 1000px;
  color: white;
  margin: 70px auto 0 auto;
}

.search-view-header {
  height: 150px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.search-view-search-bar {
  margin-top: 50px;
  width: 500px;
  height: 50px;
  padding: 10px 20px;
  border-radius: 3px;
  border: none;
  font-size: 1.2em;
  font-family: 'Roboto Condensed', sans-serif;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  outline-color: rgba(255, 255, 255, 0.7);
}

.search-view-loading {
  text-align: center;
}


